import Img8 from "../imagens/pexels-tima-miroshnichenko-6615076.jpg"

export default props => {
    return (
        <>
            <div id="conteudodetexto">
        <h3><strong>O que é Sistema Internacional De Unidades</strong></h3>
            <p>Sistema Internacional de Unidades, também conhecido pela sigla SI, 
                é a versão moderna do sistema métrico, descoberto na França durante a revolução francesa, 
                que surgiu devido a necessidade de um padrão único, pois naquela época havia muitos padrões, 
                o que gerava muita confusão aos setores de comércio e indústria. 
                O SI é um sistema que basicamente gira em torno de sete unidades de medidas, são elas: 
                ampere, kelvin, segundo, metro, quilograma, candela e mole.</p>
            <p>O ampere é representado pelo símbolo (A) e corresponde a corrente elétrica;
            </p>
            <p>o kelvin tem como símbolo (K) e corresponde a temperatura;</p>
            <p>o segundo tem como símbolo (s) e corresponde a tempo;
            </p>
            <p>o metro tem como símbolo (m) e corresponde à distância;</p>
            <p>o quilograma como símbolo (Km) e corresponde a massa;</p>
            <p>a candela tem como símbolo (cd) e corresponde a intensidade luminosa;
            </p>
            <p>o mole tem como símbolo (mol) e corresponde a quantidade de substância.
            </p>
            <p>E assim foi desenvolvido o SI em 1960, baseando-se no sistema metro-quilograma-segundo. 
                Sendo assim, por não ser um sistema estático, as unidades podem ser criadas e as modificações
                 podem ser feitas através de acordos internacionais, </p>
            <p>conforme os padrões e necessidades das medições mudam, como a necessidade de medidas mais precisas e exatas etc.
            </p>
            
            <img className='Img' src={Img8}/>
        
            <h3><strong>Como coverter medidas de Área</strong></h3>
            <h3>Como calcular área</h3>
                <p> A unidade de medida de área faz parte da Geometria,
                         e essa medida possui uma gama bastante extensa de aplicações, 
                         no dia a dia por exemplo nós a usamos constantemente, 
                         e muitas vezes nem percebemos, como por exemplo, 
                         para medir um piso que gostaria de ter em sua casa, 
                         o hectare de um terreno desejado, ou seu próprio terreno, 
                         são diversas as aplicações dessa medida em nosso dia a dia.
                </p> 

                <p>A medida de área é calculada através 
                        de um plano de duas dimensões sendo a área o produto e o 
                        resultado desse cálculo, esse plano é o de comprimento x largura x altura x base. 
                        Há algumas figuras geométricas, que já tem expressões matemáticas próprias, 
                        para o cálculo de suas áreas, o que ajuda muito.</p>

                <p>As unidades usuais de áreas, segundo  o SI (Sistema Internacional de Unidades), são essas:</p>
                    <br/>km² = quilômetro quadrado
                    <br/>hm² = hectômetro quadrado
                    <br/>dam² = decâmetro quadrado
                    <br/>m² = metro quadrado
                    <br/>dm² = decímetro quadrado
                    <br/>cm² = centímetro quadrado
                    <br/>mm² = milímetro quadrado

                <p>Para calcular a área em uma região plana, 
                    todas as dimensões devem estar em uma mesma 
                    unidade de comprimento, as unidades de comprimento 
                    segundo o SI são representadas assim:
                </p>
               
                <br/>km = quilômetro
                    <br/>hm = hectômetro
                    <br/>dam = decâmetro
                    <br/>m = metro
                    <br/>dm = decímetro
                    <br/>cm = centímetro
                    <br/>mm = milímetro
                    
                <p>Sendo assim, o método de conversão de área é muito parecido que o de comprimento.</p>

                <p>Para converter medidas de área utilize esse método:</p>
                <p>Construa uma espécie de tabela para ajudar, essa tabela deve seguir a ordem de precedência, ela ficaria assim:</p>
                
                <p>Da esquerda para direita multiplica por 100 </p>
                    
                <p>km² --- hm²--- dam²--- m²--- dm²--- cm²--- mm²</p>
                <p>Agora, sempre que for calcular uma medida, e a ordem seguir 
                        da esquerda para direita, você deve multiplicar por 100 de unidade 
                        em unidade até chegar na que você quer, exemplo: quero converter 
                        de 4 km² para m² então devo multiplicar 4 por 100 de km² até m² , 
                        o resultado seria 4 km² vale 4000000 m². Use nosso conversor para conferir os cálculos.
                </p>

                <p>Da direita para a esquerda divide por 100</p>
                    
                <p>Agora, quando o cálculo for da esquerda para direita, você divide por 100, 
                        vamos usar o mesmo exemplo, só que agor,a queremos saber quantos quilômetros 
                        quadrados (km²) são 4000000 metros quadrados (m²), o resultado é 4000000 m² são 4 km², 
                        pois dividindo por 100, 4000000 m² até  km² o resultado é 4.
                </p>
                
            <img src={Img7}/>    

            <h3><strong>Como converter Medidas De Comprimento</strong></h3>
                <p>As unidades de medidas de comprimento são extremamente necessária, 
                    pois precisamos delas para medir vários tipos de distâncias. 
                    Existem muitas unidades de medidas de comprimento, 
                    a mais conhecida e utilizada entre ela porém, é o metro, e 
                    os muitos quilômetros, hectômetros e decâmetros, e seu submúltiplos o 
                    decímetro, centímetro e milímetro. Além dessas, existem mais algumas, 
                    como as que são usadas as partes do nosso corpo como parâmetro: são elas o palmo,
                    o pé, e a polegada. Todas essa fazem parte do SI (Sistema Internacional de Unidades, 
                    mas, há também aquelas que não são do sistema internacional, mas são utilizadas dependendo 
                    da região, como a légua, a jarda, a milha e o ano-luz.</p>
    
            <h3><strong>Quais são as Unidades De Comprimento</strong></h3>
                <p>Des de muito tempo, medir distancias entre dois pontos, 
                    é uma tarefa executada por nós.
                    Ainda antigamente como não havia tecnologia ou a globalização de hoje, então não era necessário
                    que as medidas fossem padronizadas, e ele nem mesmo tinham informações matemáticas 
                    para ajudar, então usavam coisas ou objetos do dia a dia como referênciais, 
                    como cordas ou o próprio corpo humano. Hoje com a padronização tudo ficou mais fácil.
                    Segundo O SI a medida padrão de comprimento é o metro. </p>
        
                    <p><strong>Para poder medir as distâncias maiores, 
            existem os conhecidos como múltiplos do metro, que são:</strong></p>

            <strong>decâmetro</strong>: 1 decâmetro = 10 metros,|
            <strong>hectômetro</strong>: hectômetro: 1 hectômetro = 100 metros,|
            <strong>quilômetro</strong>: 1 quilômetro = 1000 metros.|

            <p/>Para poder medir as distâncias menores, existem os conhecidos como submúltiplos do metro, que são:

            <strong>decímetro</strong>: 10 decímetros = metro./
            
            <strong>centímetro</strong>: 100 centímetros = 1000 milímetros.
            
            <strong>milímetro</strong>: 1000 milímetros = 1 metro.

            <p>Os múltiplos e submúltiplos do metro podem aparecer por esses símbolos:</p>
            
            <br/>quilômetro → km

            <br/>hectômetro → hm

            <br/>decâmetro → dam

            <br/>metro → m

            <br/>decímetro → dm

            <br/>centímetro → cm

            <br/>milímetro → mm.

            <br/><img src={Img6}/>
    
            <h3><strong>Como Fazer Conversão De Medidas De Comprimento</strong></h3>
            <p>Para fazer a conversão de medidas de comprimento é muito fácil, 
                é melhor de visualizar e calcular se você construir uma espécie de tabela, 
                respeitando sempre a ordem para os múltiplos e submúltiplos do metro, 
                pois se não respeitar a ordem o calculo fica errado.</p>  
                
                
            <br/>Começando de:  km --- hm --- dam --- m --- dm --- cm --- mm.
               
            <br/>Fica fácil se pensar assim: da esquerda para direita multiplica por 10 e da direita para a esquerda divide por 10.
            
            <h3><strong>Existem Outras Unidades De Medidas De Comprimentos</strong></h3>
            <p>Além das medidas já apresentadas aqui, há também outras unidades de medidas de 
                comprimento ainda muito utilizadas e bastante comuns:</p>
            
            <br/>• <strong>Polegada:</strong> Muito utilizada na indústria 
            tecnológica principalmente para medir a tela de smartphones, 
            notebooks e demais aparelhos eletrônicos.
            A polegada é representada pelo número seguido de duas aspas, 
            exemplo 50” pode se ler como 50 polegadas. Uma polegada equivale a 2,54 cm.
            
            <br/>• <strong>Palmo:</strong> O Palmo é geralmente usado para medir
             objetos maiores do que os que se mede com as polegadas, hoje em dia 
             ele é pouco usado. Um palmo corresponde a 22,86 cm.
            
             <br/>• <strong>Pé:</strong> O Pé é muito utilizado hoje em 
             dia para encontrar-se nos ares, como à altura de um avião, 
             balão teleféricos etc. Para representar uma distância medida em pés,
              devemos colocar o número seguido de uma aspa, assim, 95’ se ler 95 pés. 
              Um pé equivale a 30,48 cm.
            
            
            <br/>• <strong>Jarda:</strong>A jarda é muito usada nos Estados Unidos, 
            ela é muito comum no futebol americano. Uma jarda corresponde a 0,9144 m.
            
            
            <br/>• <strong>Légua:</strong>A légua foi muito usada antigamente para medir 
            distâncias bastante grandes, sendo assim, muito comum na navegação. 
            Uma légua corresponde a 4,82803 quilômetros.
            
            <br/>• <strong>Milha:</strong> A milha é utilizada para medir distâncias maiores,
             e como a légua foi bastante comum nos povos antigos, hoje você pode ver a milha 
             sendo usada nas navegações. Uma milha corresponde a 1,60934 km.
            
            
            <br/>• <strong>Ano-luz:</strong> Anos-Luz é usado para medir distância entre astros no cosmos, 
                bastante usado pelas empresas que se relaciona com pesquisas no universo,
                 como a Nasa, e Spacex por exemplo, 
                 às vezes essa medida é confundida com medida de tempo, 
                 mas se atente, elas são diferentes. Um ano-luz vale 
                 aproximadamente 9 460 730 472 580,8 km.

                
                  
        
                    <h3><strong>Como converter Consumo De Combustível</strong></h3>
                    <p>Para fazer a conversão de consumo 
                        de combustível temos de ter em vista 
                        o consumo médio do seu automóvel, 
                        fazendo a divisão entre a distância percorrida 
                        e o volume de combustível gasto durante o percurso, 
                        se tem  resultado desse consumo. A distância e quantidade de combustível 
                        também possuem suas unidades de medida padronizadas no SI, 
                        por serem duas grandezas: a distância deve ser obtida em quilômetros (km), 
                        e o combustível, em litros (l).</p>
                    
                    <p>Para um resultado satisfatório, é necessário saber quantos quilômetros 
                        podemos percorrer com um litro, e assim poderemos tirar as outras distâncias.</p>
                    
                    <p>Matematicamente, essa razão é calculada dessa forma:</p>
                    <p>CM = S / V</p>
                    <p>O consumo médio do automóvel é representada por CM ; </p>
                    <p>A distância percorrida é = S;</p>
                    <p>Quantidade de combustível, em litros é = V;</p>
                    <p>A ideia de divisão que envolve o cálculo desse consumo médio é o que
                        da fundamento as razões.
                        </p>
                
                    <h3><strong>O que é Razão na matemática</strong></h3>
                    <p>Razão é o resultado de uma divisão, ela pode ser escrita na forma de fração ou em decimal.</p>
                    
                    <p>As razões são caracterizadas pela ordem da divisão, 
                        essa ordem influencia muito no resultado, então ter atenção às ordens 
                        é muito importante, por exemplo a razão de consumo médio de combustível 
                        é o espaço percorrido dividido pelo combustível gasto, se inverter essa ordem o
                         resultado ficará diferente e errado.</p>

                    <img src={Img5}/>

                    <h3><strong>Como converter Frequência</strong></h3>
                        <p>A frequência  é definida como o número de oscilações de uma 
                            onda, ou o números de movimentos circulares de um objeto, 
                            por um período de tempo, essas oscilações devem acontecer 
                            em ciclos, para que assim aconteça uma frequência.
                             Para poder calcular esses eventos cíclicos devemos 
                             dividir o número de eventos ocorridos pelo tempo que 
                             ele ocorreu e é isso que gera o R.P.M.</p>
                        
                        <p>O R.P.M, ou rotações por minuto, 
                            é o nome que se dá aos movimentos circulares das ondas, 
                            por isso quando se diz 20Hz, quer dizer que o objeto deu 
                            20 rotações a cada segundo. 
                        </p>
                        
                        <img src={Img4}/>

                        <p>A unidade de frequência segundo o Sistema Internacional (SI), 
                            é o Hertz HZ. O Hentz é uma homenagem Heinrich Rudolf Hertz, 
                            um grande cientista alemão  que entre 1857 e 1894 desenvolveu 
                            muitos trabalhos relacionados com a radiação eletromagnética. </p>
                        

                        <h3><strong>Como converter medida de massa</strong></h3>
                        <h3><strong>Unidades De Medidas De Massa</strong></h3>
                    <p>Para medir a massa, antigamente cada povo usava um tipo diferente de medida, 
                        porém quanto mais o mundo se conectava, a necessidade de padronização das medidas 
                        foi aumentado, foi por esse motivo também que surtiu o SI 9(Sistema Internacional de Unidades), 
                        e cada vez mais essas medidas estão sendo mais padronizadas.</p>
                        
                    
                    <p>Na nossa vida, praticamente todos os dias usamos medidas de massa, 
                        seja pra comprar pão ou para se pesar, e fazemos tudo isso e 
                        nem nus tocamos que estamos usando essas medidas pois se 
                        tornaram meio que uma necessidade do dia a dia, e por usarmos 
                        elas todos os dias, também necessitamos de fazer conversões entre ela, 
                        por exemplo pra comprar pão, ao invés de falarmos 0.9 quilogramas de pão já 
                        convertemos e falamos logo 900 gramas de pão por ser até melhor de falar e entender e na 
                        grande maioria das vezes nem notamos que estamos convertendo, fazendo com que isso tenha se 
                        tornado ate meio automático ou intuitivo.</p> 
                    
                    <p>É de suma importância entendermos que existem várias 
                        medidas de massa e que podemos usar cada uma delas quando for 
                        conveniente, como o caso do pão.</p> 
                    
                    <p>Grama: A grama vem ganhando muito espaço, 
                        sendo a medida mais comum para massa, a grama possui 
                        seus múltiplos (quilograma, hectograma e decagrama) 
                        e seus submúltiplos (miligrama, centigrama e decagrama). 
                        Para objetos mais leves usamos os submúltiplos e para objetos 
                        mais pesados usamos os múltiplos
                    </p> 
                    
                    <p>Toneladas: A tonelada é também uma medida muito comum, 
                        usada para massas muito grandes como o peso de um elefante. 
                        Em vez de dizer que algo tem, por exemplo, 5000kg, é bem mais 
                        conveniente e até mesmo mais fácil, dizer que possui 5 toneladas.
                    </p> 
                    
                    <p>Quilate: O quilate é a unidade de medida que mede o volume de massa de pedras e metais valiosos.
                       Um quilate corresponde a 0,2 gramas.</p> 
                    
                    <p>Arroba: A arroba é uma medida usada principalmente na agropecuária, pois os 
                        preços das carnes são calculadas em arroba.
                        1 arroba corresponde a 15 quilogramas.
                    </p> 
                    
                    
                    <p>Ha também a libra, onça e o grão, porém no Brasil, essas medidas de massa, não são usadas com muita frequência.</p>
                    
                        
                
                    <h3><strong>Conversão De Unidades De Medidas</strong></h3>
                    <p>A lista das siglas ou se preferir símbolos de cada unidade:</p>

                    <br/>kg → quilograma

                    <br/>hg → hectograma
                
                    <br/>dag → decagrama
 
                    <br/>g→ grama

                    <br/>dg → decigrama

                    <br/>cg → centigrama

                    <br/>mg → miligrama

                    <br/><img src={Img3}/>
                
                    <p>Representando essas grandezas nesse mesmo ordem, podemos construir uma tabela para visualizar melhor:</p>
                    
                    <p>QiloG -- HectoG -- DecaG -- Grama -- DeciG -- CeniG -- MiliG</p>
                    
                    <p>Para fazer a conversão é muito fácil, é assim: Da esquerda para direita multiplicamos 
                        por 10 e da direita para esquerda dividimos por 10. 
                    </p>
                   
                    <p>Então se quiser por exemplo transformar 5 kg em miligrama,
                         vai multiplicando por 10 de  unidade em unidade até chegar em miligrama(mg). 
                         O resultado seria 5 quilogramas são 5000000 (5 milhões de miligramas(mg)).
                    </p>
                    
                    <p>A mesma coisa acontece se quiser converter 5 miligramas em quilogramas, 
                        porém como é da direita para esquerda você divide por 10 de unidade em unidade 
                        até chegar na que você deseja. O resultado seria 5 miligramas vale 0,000005 quilogramas(kg).
                    </p>


                    <h3><strong>Como converter Temperatura</strong></h3>
                    <p>Se caracteriza temperatura ou estado térmico o 
                        estado de agitação das partículas de um corpo. 
                        Se as moléculas desse corpo estiverem muito agitadas 
                        sua temperatura vai ser alta, se estiverem pouco agitadas, 
                        sua temperatura será baixa.</p>
                    
                    <p>O termômetro é o aparelho usado para 
                        fazer as medições de temperatura, 
                        se encontra ele em três escalas: 
                        Kelvin, Fahrenheit e Celsius.</p>
                    
                    <p>O Zero absoluto, corresponde a um ponto em que a agitação molecular é zero, 
                        ou seja, as moléculas ficam completamente em repouso, 
                        sendo assim a menor temperatura que um corpo pode alcançar.
                    </p>
                    
                    <p>Essas escalas foram descobertas por William 
                        Thompson, um cientista inglês em meados do século XIX. 
                        Assim, o zero absoluto tem os seguintes valores: 0 Kelvin 
                        corresponde a -273,15 ºC Graus Celsius.
                    </p>
                    
                    <img src={Img1}/>
                   

                
                    <h3><strong>Escalas Termométricas</strong></h3>
                    <p>As escalas mais usadas e também as mais conhecidas 
                        atualmente são a Fahrenheit (oF), Celsius (oC) e Kelvin (K).
                         Cada uma delas tem um ponto fixo, 
                         esse ponto é diferentes em cada uma delas: 
                         a escala de Celsius por é a mais usada,
                          muitos países a adotaram como medida principal de temperatura, 
                          a Fahrenheit é mais usada nos EUA, e a Kelvin é uma escala absoluta,
                           mais usada no meio científico por ser uma escala mais técnica.</p>
                    
                    <h3><strong>Expressões Para Conversão De Temperaturas</strong></h3>
                    
                    <p>Para deduzir as relações de transformação, 
                        devemos partir do ponto fixo de cada uma das escalas, 
                        assim teremos essas fórmulas:
                    </p>
                    
                    <p>Temp cel = temperatura em Celsius </p>
                    <p>Temp kel = temperatura em Kelvin </p>
                    <p>Temp Fa = temperatura em Fahrenheit </p>
                    <p>Temp celc = Tem kel - 2 7 3, 1 5 </p>
                    <p>Temp celc = 5 / 9 Temo Fa - 32 </p>

                    <br/><img src={Img2}/>
                    
                    <h3>Conversor Rápido</h3>
                    <p>O intuito dos conversores de medidas é facilitar a vida das pessoas
                        criando uma forma rápida e fácil de se converter medidas, o que na correria do  no dia
                        a dia não seria muito viável, fazer os calculos a mão leva tempo, coisa que 
                        nos dias de hoje é valioso.
                    </p>
                    <p>Foi pensando nisso que foi feita a criação desse site, pensando na rapidez de resposta e
                        simplicidade. Aqui é oferecido as conversões mais usadas para facilitar a vida do usuário.
                         
                    </p>

                    <p>Existem muitos outros tipos de conversões porém colca-los todos, ficaria muito cheio e dificultoso
                        para o usuário, tirando o proposito do site, que é ser rápido e eficaz.</p>
                    
                    <p>Esquero que o site tenha ajudado, obrigado pela visita!</p>
            </div>
        </>

    )
}